import './App.css';

const RateCats = () => {

  const listCats = () => {
    return [1,2,3,4,5].map((i: number) => {
      const imgUrl = process.env.PUBLIC_URL + `/Cat${i}.jpg`;
      console.log('imgUrl', imgUrl);
      return (
        <div key={i} style={{display: 'flex', flexDirection: 'column', margin: '8px 8px'}}>
          <img src={imgUrl} alt={`Cat${i}`} style={{width: '480px'}} />
          <p>Rating: ...</p>
        </div>
      );
    });
  }

  return (
    <div>
      <header className="App-header">
        <p>Rate some cats!</p>
      </header>
      <div style={{display: 'flex', flexWrap: 'wrap'}}> {listCats()}</div>
      <div style={{textAlign: 'left', margin: '30px'}}>
        <input type="checkbox" id="subscribe" />Subscribe to our mailing list to get updates to your email inbox.
      </div>
    </div>
  );
}

export default RateCats;
